import React, { useState } from 'react';
import { Select, Space } from 'antd';

const { Option } = Select;

const App: React.FC<{ onSelected: any }> = ({ onSelected }) => {

    const [optDisabled, setOptDisabled] = useState(false);

    const handleChange = (value: string[]) => {
        console.log(`selected ${value}`);
        setOptDisabled(value.length >= 3);
        console.log(`optDisabled ${optDisabled}`);
        onSelected(value);
    };

    const handleDropdownVisibleChange = (open: boolean) => {
        console.log(`open ${open}`);
    };

    let roles = [
        {value:"471__",name:" ()",label:"",alis:""},
        {value:"472__",name:" ()",label:"",alis:""},
        {value:"473__",name:" ()",label:"",alis:""},
    ];

    let opts = roles.map((role) => 
        <Option value={role.value} label={role.label}>
            <Space>
                <span role="img" aria-label={role.label}>
                {role.alis}
                </span>
                {role.name}
            </Space>
        </Option>)

    return (
        <Select
            mode="multiple"
            // style={{ width: '100%' }}
            style={{ width: 300 }}
            placeholder="select one country"
            defaultValue={['china']}
            onChange={handleChange}
            optionLabelProp="label"
            //maxTagCount={2}
            {...(optDisabled && { open: false, onDropdownVisibleChange: handleDropdownVisibleChange })}
        >
            {opts}
        </Select>
    )
};

export default App;