import React, { useState } from 'react';
import type { FC } from 'react';
import Select from './components/Select'
import { Button, Breadcrumb, Layout, Menu, theme } from 'antd';
import { Card, Space } from 'antd';
import 'antd/dist/reset.css';
import './App.css';
import type { SizeType } from 'antd/es/config-provider/SizeContext';

import monster_471 from './asset/images/monster_471.jpg';
import monster_472 from './asset/images/monster_472.jpg';
import monster_473 from './asset/images/monster_473.jpg';

const { Header, Content, Footer } = Layout;

export function isValidKey(key: string | number | symbol, object: object): key is keyof typeof object {
  return key in object;
}

const App: React.FC = () => {

  const {
    token: { colorBgContainer },
  } = theme.useToken();

  const [size, setSize] = useState<SizeType>('middle');
  const [selectRoles, setSelectRoles] = useState<string[]>([]);
  const [againstRoles, setAgainstRoles] = useState<[MCard[]]>([[]]);

  const queryAgainst = () => {
    console.log(`selectRoles ${selectRoles}`);

    let queryKey = selectRoles.map(i => i.split('_')[0]).sort().join('_');
    if(isValidKey(queryKey,againstList)){
      let against = againstList[queryKey]
      setAgainstRoles(against)
    }
  }

  type MCard = {
    name: string;
    img: string;
  }

  type AgainstMap = {
    [key: string]: [MCard[]];
  }

  const againstList: AgainstMap = {
    "471_472_473": [
      [{ name: "", img: monster_471 }, { name: "", img: monster_472 }, { name: "", img: monster_473 }]
    ]
  }

  return (
    <Layout className="layout">
      <Header>
        <div className="logo" />
      </Header>
      <Content style={{ padding: '0 50px' }}>
        <div className="site-layout-content" style={{ background: colorBgContainer }}>
          <Select onSelected={setSelectRoles} />
          <Button type="primary" onClick={() => queryAgainst()} size={size}>
            
          </Button>
          <div className="site-layout-content" style={{ background: colorBgContainer }}>
            <Space direction="vertical" size={16}>
              {
                againstRoles && againstRoles.map(i => 
                  <Space direction="horizontal" size={16}>
                    {
                      i.map(a => <Card title={a.name} extra={<a href="#">More</a>} style={{ width: 150 }}
                      hoverable
                      cover={<img alt="example" src={a.img} />}
                    >
                    </Card>)
                    }
                  </Space>)
              }
            </Space>
          </div>
        </div>
      </Content>
      <Footer style={{ textAlign: 'center' }}>Created by Yuchi</Footer>
    </Layout>
  );
};

export default App;
